<template>
  <div class="loginContainer">
    <table>
      <thead>
        <h3>Login</h3>
      </thead>
      <tbody @keyup.enter="toHome">
        <div>
          <input
            type="text"
            placeholder="请输入用户名或账户"
            v-model="username"
          />
        </div>
        <div>
          <input type="password" placeholder="请输入密码" v-model="password" />
        </div>
        <div class="autologin">
          <el-checkbox
            v-model="checked"
            label="自动登录"
            text-color="#fff"
          ></el-checkbox>
          <p>没有账户?<a @click="toRegister">注册</a>一个吧</p>
        </div>
        <button class="loginBtn" @click="toHome">登录</button>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checked: true,
      username: '',
      password: ''
    }
  },
  methods: {
    toRegister() {
      this.$router.push('/register')
    },
    async toHome() {
      let { username, password, checked } = this
      if (username && password) {
        let obj = { username, password }
        const res = await this.$store.dispatch('reqLogin', obj)
        //res为0或者1,1为账户密码正确
        if (res.code) {
          if (checked) {
            localStorage.setItem('smartToken', res.token)
          }
          this.$message({
            message: '登录成功',
            type: 'success'
          })
          //this.$router.push('/home')
        } else {
          this.$message.error('用户名或密码错误')
        }
      } else {
        this.$message({
          message: '请输入用户名和密码',
          type: 'warning'
        })
      }
    }
  },
  computed: {}
}
</script>

<style scoped>
.loginContainer {
  width: 80vh;
  height: 50vh;
}
.loginContainer:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 5%;
  filter: blur(-3px);
  box-shadow: rgb(0 0 0 / 10%) 0 5px 20px;
  background-color: var(--w-alpha-80);
  backdrop-filter: blur(15px) saturate(180%);
  z-index: -1;
}
.loginContainer table {
  text-align: center;
  width: 100%;
  height: 100%;
  color: #fff;
}
/* .loginContainer table thead {
  text-align: center;
} */
.loginContainer table thead h3 {
  font-weight: 700;
  font-size: 40px;
  margin: 20px 0;
}
.loginContainer table tbody input {
  width: 25vw;
  height: 4vh;
  min-width: 436px;
  border: 0;
  border-radius: 5px;
  margin: 20px auto;
  padding-left: 10px;
}
.loginContainer table tbody .autologin {
  display: flex;
  justify-content: space-around;
  font-size: 16px;
}
.el-checkbox {
  color: #fff;
}
.loginContainer table tbody p a {
  color: #00f;
  cursor: pointer;
}
.loginBtn {
  margin-top: 20px;
  padding: 10px 40px;
  border: 0;
  border-radius: 6px;
  background-color: #409eff;
  color: #fff;
  font-size: 18px;
}
</style>
