import LoginAndregister from '@/views/loginAndregister'
import Login from '@/views/loginAndregister/login'
import Register from '@/views/loginAndregister/register'
import Home from '@/views/home'

export default [
    {
        path: '/',
        component: LoginAndregister,
        children: [
            {
                path: 'login',
                component: Login,
            },
            {
                path: 'register',
                component: Register,
            }, {
                path: '/',
                redirect: '/login'
            }
        ]
    }, {
        path: '/home',
        component: Home,
    }
]