<template>
  <div ref="bgStyle" class="container">
    <router-view class="lORr"></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {}
  },
  created() {
    this.getback()
  },
  methods: {
    async getback() {
      const res = await this.$store.dispatch('reqBing')
      this.$refs.bgStyle.style.backgroundImage = `url(${res})`
    }
  }
}
</script>

<style scoped>
.container {
  width: 100vw;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  /* display: flex; */
}
.lORr {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>
