import requests from "@/API/requests";
//请求背景图片
export const getBingBackground = () => requests({ url: '/bgstyle', method: 'get' })
//请求login登录
export const getUsersLogin = (params) => requests({ url: '/userslogin', method: 'get', params })
//请求register注册
export const getUsersRegister = (params) => requests({ url: '/usersregister', method: 'get', params })
//请求home页面
export const getUsersHome = () => requests({ url: '/homedata', method: 'get' })
