import Vue from 'vue'
import Vuex from 'vuex'
import { getBingBackground, getUsersLogin, getUsersRegister, getUsersHome } from '@/API/index.js'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
  },
  mutations: {
  },
  actions: {
    //请求背景图片
    async reqBing ({ commit }) {
      const res = await getBingBackground()
      if (res) {
        return res
      } else {
        return Promise.reject(new Error('faile'))
      }
    },
    //请求登录
    async reqLogin ({ commit }, params) {
      const res = await getUsersLogin(params)
      if (res) {
        return res
      } else {
        return Promise.reject(new Error('faile'))
      }
    },
    //请求注册
    async reqRegister ({ commit }, params) {
      const res = await getUsersRegister(params)
      if (res) {
        return res.code
      } else {
        return Promise.reject(new Error('faile'))
      }
    },
    //请求home页
    async reqHome () {
      try {
        const res = await getUsersHome()
        return res
      } catch (error) {
        return Promise.reject(new Error('faile'))
      }
    },
  },
  modules: {
  }
})
