<template>
  <div class="homeContainer">
    <div class="header">
      <h1>大浪,该吃药啦</h1>
      <div>
        <a>用户名</a>
        <a>取药记录</a>
      </div>
    </div>
    <div class="body">
      <div class="cabinetItem"></div>
      <div class="cabinet">
        <div class="pharmacy">
          <img @click="toSetting" src="~@/assets/add.png" alt="" />
        </div>
        <el-button type="primary" @click="getHomeData">一键取药</el-button>
      </div>
    </div>
    <table class="setting" v-show="isShow">
      <thead>
        <h3>设置药柜信息</h3>
      </thead>
      <tbody>
        <div>
          <label for="input1">柜子名: </label
          ><input type="text" id="input1" v-model="cabinetName" />
        </div>
        <div>
          <label for="input2">柜子宽: </label
          ><input type="text" id="input2" v-model="cabinetWidth" />
        </div>
        <div>
          <label for="input3">柜子高: </label
          ><input type="text" id="input3" v-model="cabinetHeight" />
        </div>
        <div>
          <label for="input4">格子数(x):</label
          ><input type="text" id="input4" v-model="latticeX" />
        </div>
        <div>
          <label for="input5">格子数(y):</label
          ><input type="text" id="input5" v-model="latticeY" />
        </div>
        <div>
          <label for="input6">格子宽: </label
          ><input type="text" id="input6" v-model="latticeWidth" />
        </div>
        <div>
          <label for="input7">格子高: </label>
          <input type="text" id="input7" v-model="latticeHeight" />
        </div>

        <div>
          <button class="cancelBtn btn" @click="cancel">取消</button>
          <button class="confirmBtn btn" @click="confirm">确认</button>
        </div>
      </tbody>
    </table>
    <div class="bg" v-show="isShow"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //是否显示柜子设置界面
      isShow: false,
      //柜子信息数据
      cabinetName: '',
      cabinetWidth: '',
      cabinetHeight: '',
      latticeX: '',
      latticeY: '',
      latticeWidth: '',
      latticeHeight: '',
      arr: []
    }
  },
  methods: {
    toSetting() {
      this.isShow = true
    },
    cancel() {
      this.isShow = false
    },
    confirm() {
      let {
        cabinetName,
        cabinetWidth,
        cabinetHeight,
        latticeX,
        latticeY,
        latticeWidth,
        latticeHeight
      } = this
      this.isShow = false
      // cabinetName&&cabinetWidth&&cabinetHeight&&latticeX&&latticeY&&latticeWidth&&latticeHeight&&
    },
    async getHomeData() {
      try {
        const res = await this.$store.dispatch('reqHome')
        console.log(res)
      } catch (error) {
        this.$message.error('请重新登录')
        this.$router.push('/login')
      }
    }
  }
}
</script>

