<template>
  <div class="loginContainer">
    <table>
      <thead>
        <h3>Register</h3>
      </thead>
      <tbody>
        <div>
          <input
            type="text"
            placeholder="请输入用户名或账户"
            v-model="username"
          />
        </div>
        <div>
          <input type="password" placeholder="请输入密码" v-model="password" />
        </div>
        <button @click="toLogin">注册</button>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    async toLogin() {
      let { username, password } = this
      if (username && password) {
        let obj = { username, password }
        const res = await this.$store.dispatch('reqRegister', obj)
        if (res) {
          this.$message({
            message: '注册成功',
            type: 'success'
          })
          this.$router.push('/login')
        } else {
          this.$message.error('用户名已存在')
        }
      } else {
        this.$message({
          message: '请输入用户名和密码',
          type: 'warning'
        })
      }
    }
  }
}
</script>

<style scoped>
.loginContainer {
  width: 80vh;
  height: 50vh;
}
.loginContainer:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 5%;
  filter: blur(-3px);
  box-shadow: rgb(0 0 0 / 10%) 0 5px 20px;
  background-color: var(--w-alpha-80);
  backdrop-filter: blur(15px) saturate(180%);
  z-index: -1;
}
.loginContainer table {
  text-align: center;
  width: 100%;
  height: 100%;
  color: #fff;
}
.loginContainer table thead h3 {
  font-weight: 700;
  font-size: 40px;
  margin: 20px 0;
}
.loginContainer table tbody input {
  width: 25vw;
  min-width: 436px;
  height: 4vh;
  border: 0;
  border-radius: 5px;
  margin: 20px auto;
  padding-left: 10px;
}
.loginContainer table tbody p a {
  color: #00f;
  cursor: pointer;
}
.loginContainer table tbody button {
  margin-top: 10px;
  padding: 10px 40px;
  border: 0;
  border-radius: 6px;
  background-color: #409eff;
  color: #fff;
  font-size: 18px;
}
</style>
